.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.footer{
  height: 100%;
  background-color:black;
  opacity: 0.9;
}
.particles{
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;
  z-index:-1;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.about{
  height: 100%;
  background-color: dimgrey;
  opacity: 0.5;
}
.prb{
  background-image: url("bg2.jpeg");
}
.content{
  font-family: Arial;
}
.pr{
  height: 100%;
  background-color: dimgrey;
  opacity: 0.8;
}
.pcb{
  background-image: url("bg1.jpeg");

}
.pc{
  height: 100%;
  background-color: black;
  opacity: 0.6;
}
.contact{
  height: 100%;
  background-color:dimgrey;
  opacity: 0.5;
}
.services{
  height:100vh;
  background-color: lightskyblue;
  opacity: 0.7;
}
.App-link {
  color: #61dafb;
}
.ab{
  font-size: x-large;
}
.footer{
/*position: sticky;*/
  left: 0px;
  bottom: 0px;
}
#navbar{
  position: sticky;
  top: 0px;
  z-index: 100;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
